.p-works__title {
    margin-bottom: var(--spacing-l);
    text-align: center; }


.p-works__cont {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    margin-bottom: var(--spacing-m);
    padding: var(--spacing-l);
    background-color: var(--color-main);
    &:nth-last-child(1) {
        margin-bottom: 0; }
    @include md {
        flex-flow: column nowrap; } }


.p-works__box {
    flex: 0 1 calc(100% / 2);
    @include md {
        margin-bottom: var(--spacing-l); } }


.p-works__box__target {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: var(--spacing-s);
    @include lg {
        &:hover {
            .p-works__box__img {
                transform: scale(1.05); }
            .p-works__box__mask {
                opacity: 1; } } } }


.p-works__box__img {
    width: 100%;
    height: auto;
    transition: all 0.4s ease-in-out; }


.p-works__box__mask {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    transition: all 0.4s ease-in-out; }


.p-works__description {
    flex: 0 1 calc(100% / 2);
    width: 100%;
    margin-left: var(--spacing-m);
    @include md {
        margin-left: 0; } }


.p-works__description__heading,
.p-works__description__text,
.p-works__description__uses,
.p-works__description__source {
    text-align: center; }


.p-works__description__heading,
.p-works__description__text {
    margin-bottom: var(--spacing-m); }


.p-works__description__uses {
    margin-bottom: var(--spacing-s);
    color: var(--color-gray); }


.p-works__description__source__target {
    padding: var(--spacing-xx-s);
    color: var(--color-blue);
    transition: all 0.2s ease;
    &:hover {
        opacity: 0.7; } }


.c-color-list {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end; }


.c-color-item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 16px;
    height: 24px;
    background-color: white;
    margin-left: var(--spacing-x-s);
    text-align: right;
    transform: skew(-15deg, 0deg);
    @include md {
        width: 14px;
        height: 22px; }
    @include sm {
        width: 12px;
        height: 20px; } }


.c-color-base,
.c-color-main,
.c-color-accent {
    width: 14px;
    height: 22px;
    @include md {
        width: 12px;
        height: 20px; }
    @include sm {
        width: 10px;
        height: 18px; } }


.c-color-base {
    background-color: var(--color-base); }


.c-color-main {
    background-color: var(--color-main); }


.c-color-accent {
    background-color: var(--color-blue); }


.c-color-font-01 {
    background-color: var(--color-white); }


.c-color-font-02 {
    background-color: var(--color-gray); }

